// import ContentDetail from "@/components/ContentDetail"; //文章列表详情页面
// import Test from "./views/activity/ActivityDetail"; //活动详情页面

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  // 切换路由返回页面顶部
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: "/",
      name: "homepage",
      component: () => import("./views/homepage/index.vue") //主页
    },
    {
      path: "/about",
      name: "about",
      component: () => import("./views/about/index.vue") //关于
    },
    {
      path: "/product",
      name: "product",
      component: () => import("./views/product/index.vue") //产品
    },
    {
      path: "/news",
      name: "news",
      component: () => import("./views/news/index.vue") //新闻
    },
    {
      path: "/activity",
      name: "activity",
      component: () => import("./views/activity/index.vue") //活动
    },
    {
      path: "/videopage",
      name: "videopage",
      component: () => import("./views/videopage/index.vue") //视频
    },
    {
      path: "/join/:type",
      name: "join",
      component: () => import("./views/join/index.vue") //加入我们
    },
    {
      path: "/copyright",
      name: "copyright",
      component: () => import("./views/otherpages/Copyright.vue") //版权信息
    },
    {
      path: "/contentDetail/:id",
      name: "contentDetail",
      component: () => import("@/components/ContentDetail"), //文章列表详情页面
      meta: {
        keepAlive: false // 不需要缓存
      }
    },
    {
      path: "/activityDetail/:id",
      name: "activityDetail",
      component: () => import("./views/activity/ActivityDetail"), //活动详情页面
      meta: {
        keepAlive: false // 不需要缓存
      }
    }
  ]
});
